.swipebuttons{
    position:fixed;
    bottom:5vh;
    display: flex;
    width: 100%;
    justify-content: space-evenly;
}

.swipebuttons .MuiIconButton-root{
    background-color: white;
    box-shadow: 0px 10px 53px 0px  rgb(0 ,0 ,0 ,0.3) !important;
}

.swipebuttons_repeat{
    padding: 3vw;
    color:#f5b74b !important;
}
.swipebuttons_left{
    padding: 3vw;
    color:#ec5e6f !important;
}
.swipebuttons_star{
    padding: 3vw;
    color:#62b4f9 !important;
}
.swipebuttons_right{
    padding: 3vw;
    color:#76e2b3 !important;
}
.swipebuttons_lightning{
    padding: 3vw;
    color:#915dd1 !important;
}